.Overlay {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 4;
    top: 0px;
    left: 0px;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0, 0.9);
    overflow-x: hidden;
    font-family: Roboto, serif;
    font-size: 30px;
    font-weight: 400;
    transition: 0.5s;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.Overlay a {
    padding: 8px;
    text-decoration: none;
    font-size: 36px;
    color: #818181;
    display: block;
    transition: 0.3s;
}

.Overlay a:hover, .Overlay a:focus {
    color: #f1f1f1;
}

.Overlay .closebtn {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
}

@media screen and (max-height: 450px) {
    .Overlay a {font-size: 20px}
    .Overlay .closebtn {
        font-size: 40px;
